import { IProductInputState } from "../../features/productInput/productInputSlice";

export const validate: {
  [K in keyof IProductInputState]?: (value: string) => {
    isValid: boolean;
    errMsg: string;
  };
} = {
  status: (value) => {
    if (value) {
      return { isValid: true, errMsg: "" };
    }
    return { isValid: false, errMsg: " " };
  },
  productNm: (value) => {
    if (value.length > 0) {
      return { isValid: true, errMsg: "" };
    }
    return { isValid: false, errMsg: " " };
  },
  platformNm: (value) => {
    if (value.length > 0) {
      return { isValid: true, errMsg: "" };
    }
    return { isValid: false, errMsg: " " };
  },
  category: (value) => {
    if (value.length > 0) {
      return { isValid: true, errMsg: "" };
    }
    return { isValid: false, errMsg: " " };
  },
  subCategory: (value) => {
    if (value.length > 0) {
      return { isValid: true, errMsg: "" };
    }
    return { isValid: false, errMsg: " " };
  },
  price: (value) => {
    if (value.length > 0) {
      return { isValid: true, errMsg: "" };
    }
    return { isValid: false, errMsg: " " };
  },
  shippingPrice: (value) => {
    if (value.length > 0) {
      return { isValid: true, errMsg: "" };
    }
    return { isValid: false, errMsg: " " };
  },
  freeShippingPrice: (value) => {
    if (value.length > 0) {
      return { isValid: true, errMsg: "" };
    }
    return { isValid: false, errMsg: " " };
  },
  freeShippingYn: (value) => {
    if (value === "Y" || value === "N") {
      return { isValid: true, errMsg: "" };
    }
    return { isValid: false, errMsg: " " };
  },
};
