import styled from "styled-components";
import { Row, TextMain } from "../../../shared/ui/styledComps";
import SquareInput from "../../../shared/ui/SquareInput";
import { useAppDispatch, useAppSelector } from "../../../app/reduxStore/hooks";
import {
  IProductInputState,
  setValue,
} from "../../../features/productInput/productInputSlice";

const NUTRITION_LIST: {
  key: keyof IProductInputState;
  title: string;
  unit: string;
}[] = [
  { key: "content", title: "", unit: "g" },
  { key: "calorie", title: "", unit: "kcal" },
  { key: "sodium", title: "", unit: "mg" },
  { key: "carb", title: "", unit: "g" },
  { key: "sugar", title: "", unit: "g" },
  { key: "fiber", title: "", unit: "g" },
  { key: "fat", title: "", unit: "g" },
  { key: "transFat", title: "", unit: "g" },
  { key: "saturatedFat", title: "", unit: "g" },
  { key: "protein", title: "", unit: "g" },
  { key: "cholesterol", title: "", unit: "mg" },
];

const Nutrition = () => {
  // redux
  const dispatch = useAppDispatch();
  const productInputState = useAppSelector((state) => state.productInput);

  return (
    <Container>
      {NUTRITION_LIST.map((nutrition) => (
        <Row key={nutrition.key} style={{ width: 180 }}>
          <TextMain style={{ flex: 1, fontSize: 14 }}>
            {nutrition.title} ({nutrition.unit})
          </TextMain>
          <SquareInput
            style={{ width: 64 }}
            value={productInputState[nutrition.key].value}
            onChange={(v) =>
              dispatch(setValue({ key: nutrition.key, value: v.target.value }))
            }
          />
        </Row>
      ))}
    </Container>
  );
};

export default Nutrition;

const Container = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  flex-wrap: wrap;
  row-gap: 16px;
  column-gap: 40px;

  margin-top: 24px;
`;
